// @flow
import React, { useState } from 'react';
import styled from 'styled-components';

import Section from 'shared/Section';
import Button from 'shared/Button';
import InputText from './InputText';
import Textarea from './Textarea';

import { CONTACT_SECTION } from 'data/sections';
import { MY_EMAIL } from 'data/about_me';

/**
 * Serialize object into URI string
 */
function serializeParam(obj) {
  return Object.keys(obj)
    .map(k => `${encodeURIComponent(k)}=${encodeURIComponent(obj[k])}`)
    .join('&');
}

const Form = styled.form`
  display: flex;
  flex-wrap: wrap;
`;

const RespondMsg = styled.span`
  margin-right: 10px;
  font-style: italic;
`;

const FormControl = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
`;

function ContactSection() {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    message: '',
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [respondMsg, setRespondMsg] = useState('');
  let respondMsgTimeout: TimeoutID | null = null;

  const handleInputChange = (
    e: SyntheticKeyboardEvent<HTMLInputElement | HTMLAreaElement>
  ) => {
    const eventTarget: any = e.currentTarget;
    const { name: inputName, value: inputValue } = eventTarget;

    setFormData({
      ...formData,
      [inputName]: inputValue,
    });
  };

  /**
   * Show respond message and clear it with timeout
   */
  const handleRespondMsgShow = (respondMsg: string) => {
    setRespondMsg(respondMsg);

    if (respondMsgTimeout) {
      clearTimeout(respondMsgTimeout);
    }

    respondMsgTimeout = setTimeout(() => {
      setRespondMsg('');
    }, 3000);
  };

  /**
   * Submit form to Formspree
   */
  const handleFormSubmit = (e: SyntheticEvent<HTMLFormElement>) => {
    e.preventDefault();

    // Set loading
    setIsSubmitting(true);

    // Send Email by Formspree
    fetch(`//formspree.io/${MY_EMAIL}`, {
      method: 'post',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/x-www-form-urlencoded',
      },
      body: serializeParam(formData),
    })
      .then(response => response.json())
      .then(({ success }) => {
        if (!success) {
          setIsSubmitting(false);
          handleRespondMsgShow('Something Wrong... (ã£- â¸ â Ï)');
        } else {
          setFormData({
            name: '',
            email: '',
            message: '',
          });
          setIsSubmitting(false);
          handleRespondMsgShow('Thank You  ~ã¾(ï¼¾âï¼¾)');
        }
      });
  };

  return (
    <Section id={CONTACT_SECTION.id} title={CONTACT_SECTION.title}>
      <Form onSubmit={handleFormSubmit}>
        <InputText
          required
          name="name"
          value={formData.name}
          placeholder="Your name"
          onChange={handleInputChange}
        />
        <InputText
          required
          type="email"
          name="email"
          value={formData.email}
          placeholder="Email"
          onChange={handleInputChange}
        />
        <Textarea
          required
          name="message"
          value={formData.message}
          placeholder="The person you call is unavailable now, please leave down the message I will reply you later :D"
          onChange={handleInputChange}
        />

        <FormControl>
          {respondMsg && <RespondMsg>{respondMsg}</RespondMsg>}
          <Button
            solid
            type="submit"
            color="primary"
            disabled={
              isSubmitting ||
              (!formData.name || !formData.email || !formData.message)
            }
          >
            {isSubmitting ? 'Sending...' : 'Send Message'}
          </Button>
        </FormControl>
      </Form>
    </Section>
  );
}

export default ContactSection;
